import React from 'react';
import './App.css';
import Header from './Header';
import Form from './Form';
import Result from './Results';
import Dict from './tenLetters';

const key = {
    2: 'ABC',
    3: 'DEF',
    4: 'GHI',
    5: 'JKL',
    6: 'MNO',
    7: 'PRS',
    8: 'TUV',
    9: 'WXY',
    0: 'Z'
};

let dictionary = Dict();

function printWordsUtil(numbers, curr, output, n, words) {
    if (curr === n) {
        return output;
    }

    // Try all 3 possible characters for current digits in number[] and recur for remaining digits
    for (let i = 0; i < key[numbers[curr]].length; i++) {
        output += key[numbers[curr]].charAt(i);
        let temp = printWordsUtil(numbers, curr + 1, output, n, words);
        if (typeof temp === 'string' || temp instanceof String) {
            console.log(dictionary.indexOf(temp.toLowerCase()));
            if (dictionary.indexOf(temp.toLowerCase()) !== -1) {
                words.push(temp);
            }
        }
        output = output.substring(0, output.length - 1);
    }
    return words;
}

class App extends React.Component {
    state = {
        input: {},
        valid: false,
        hasValue: false,
        words: [],
        loading: false
    };

    onChange = (input) => {
        this.setState({valid: !isNaN(input), hasValue: Object.keys(input).length});
    };

    onSubmit = (x) => {
        this.setState({input: x, hasValue: x.hasValue, words: []});
        let input = x.number.split('');
        const n = input.length;
        this.setState({words: printWordsUtil(input, 0, [], n, [])});
    };

    render() {
        return (
            <div>
                <Header/>
                <div className='container'>
                    <div className='row'>
                        <div className='col=9'>
                            <Form onSubmit={input => this.onSubmit(input)} onChange={input => this.onChange(input)}/>
                            <i>NOTE: 1 is not a valid number</i>
                            <Result result={this.state}/>
                        </div>
                        <div className='col-3'>
                            <h3>KEY: </h3>
                            <ul>{Object.keys(key).map(el => <li key={el}>{el}: {key[el]}</li>)}</ul>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default App;
