import React from "react";

const ShowDetail = (props) => {
    const details = props.details;

    if (details.input.number) {
        return (
            <div>
                <div className='mt-3'>You entered: {details.input.number}</div>
                {details.words.length < 1 ? <div>No Results </div> :
                    <ul>{details.words.map(el => <li key={el}> {el} </li>)}</ul>}
            </div>
        );
    } else {
        return <div/>;
    }
};

export default ShowDetail;